.w-dropdown{
  @include radius;
  @extend %transition-all;
  position: relative;
  height: $height-m;
  font-size: 14px;
  color:#fff;
  display: inline-block;
  cursor: pointer;
  white-space:nowrap;
  cursor:pointer;
  -webkit-appearance:none;
  text-align:center;
  box-sizing:border-box;
  outline:0;
  margin:0;
  -moz-user-select:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  padding:0 10px;
  &+&{
    margin-left: 10px;
  }
  [class*=w-icon-]{
    @extend %transition-all;
    margin-right:5px;
  }
  &.is-disabled{
    color:$disable-color;
    cursor:not-allowed;
    background-image:none;
    background-color:$disable-bg;
    border-color:$disable-line;
    [class*=w-icon-]{
      color:$disable-color;
    }
    span{
      color: $disable-color;
      &:after{
        color: $disable-color;
        cursor: not-allowed;
      }
    }
    &:hover{
      background-color:$disable-bg;
      border-color:$disable-line;
      span{
        color: $disable-color;
        &:after{
          color: $disable-color;
          cursor: not-allowed;
        }
      }
    }
  }
  &:hover{
    span{
      color:#fff;
      &:after{
        color:#fff;
      }
    }
  }
  span{
    @extend %transition-all;
    display: inline-block;
    height: $height-m;
    line-height:32px;
    &:after{
      @extend %transition-all;
      font-family:"icon" !important;
      font-size:20px;
      font-style:normal;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: "\e613";
      position: relative;
      top: 2px;
      color: #fff;
    }
  }
  .w-dropdown_content{
    background-color:#fff;
    border: 1px solid $line;
    position: absolute;
    top: auto;
    left: 0;
    right: auto;
    z-index: 1;
    line-height: 1.5;
    font-size: 14px;
    color: $gray;
    min-width: 100px;
    li{
      @extend %transition-all;
      position: relative;
      padding: 3px 35px 2px 10px;
      cursor: pointer;
      white-space:nowrap;
      display: block;
      text-align: left;
      &.is-disabled{
        cursor: not-allowed;
        background-color:#fff!important;
        color:$disable-color!important;
      }
      &.is-line{
        border-top: 1px solid $line;
      }
      &:hover,&.is-selected{
        background-color:$l-blue;
        color: #fff;
      }
      &.is-selected:after{
        position: absolute;
        right: 10px;
        top: 0;
        font-family:"icon" !important;
        font-size:16px;
        font-style:normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: "\e604";
      }
    }
  }
}
.w-dropdown_default{
  background:#fff;
  border:1px solid $line;
  color:$gray;
  [class*=w-icon-]{
    color:$gray;
  }
  span{
    &:after{
      color: $gray;
    }
  }
  &:hover{
    border-color:$l-blue;
    [class*=w-icon-]{
      color:$l-blue;
    }
    span{
      color:$l-blue;
      &:after{
        color:$l-blue;
      }
    }
  }
  &.is-disabled{
    &:hover{
      [class*=w-icon-]{
        color:$disable-color;
      }
    }
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$d-blue;
        color: #fff;
      }
    }
  }
}
.w-dropdown_primary{
  background:$blue;
  border:1px solid $blue;
  &:hover{
    background:$l-blue;
    border-color:$l-blue;
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$d-blue;
      }
    }
  }
}
.w-dropdown_success{
  background:$green;
  border:1px solid $green;
  color:#fff;
  &:hover{
    border-color:$l-green;
    background:$l-green;
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$d-green;
      }
    }
  }
}
.w-dropdown_warning{
  background:$yellow;
  border:1px solid $yellow;
  &:hover{
    border-color:$l-yellow;
    background:$l-yellow;
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$d-yellow;
      }
    }
  }
}
.w-dropdown_danger{
  background:$red;
  border:1px solid $red;
  &:hover{
    border-color:$l-red;
    background:$l-red;
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$d-red;
      }
    }
  }
}
.w-dropdown_link{
  background: transparent;
  border:1px solid transparent;
  color:$gray;
  [class*=w-icon-]{
    color:$gray;
  }
  span{
    &:after{
      color: $gray;
    }
  }
  &:hover{
    [class*=w-icon-]{
      color:$l-blue;
    }
    span{
      color:$l-blue;
      &:after{
        color:$l-blue;
      }
    }
  }
  &.is-disabled{
    border:none;
    background:transparent;
    &:hover{
      [class*=w-icon-]{
        color:$disable-color;
      }
      background:transparent;
    }
  }
  .w-dropdown_content{
    li{
      &:hover,&.is-selected{
        background-color:$l-blue;
      }
    }
  }
}


