@charset "utf-8";
// 颜色 l=Light代表浅色，d=Dark代表深色
$l-blue      : #4A8EE1;
$blue        : #4383D2;
$d-blue      : #2E72C5;
 
$l-green     : #7FB144;
$green       : #78A740;
$d-green     : #6B9637;
 
$l-yellow    : #E9AB15;
$yellow      : #D69E14;
$d-yellow    : #C48E08;
 
$l-red       : #D73D3D;
$red         : #CF3131;
$d-red       : #BF2222;

$gray        : #3E3E3E;
$icon        : #999;
$line        : #DDD;
$placeholder : #999;

$disable-line: #D6D6D6;
$disable-bg  : #F3F3F3;
$disable-color: #C1C1C1;

// 高度
$height-l    : 40px;
$height-m    : 36px;
$height-s    : 28px;

// 字体颜色
@each $colorlist, $color in (gray,$gray),(red,$red),(yellow,$yellow),(green,$green),(blue,$blue){
  .font-#{$colorlist} {
    color:$color+!important;
  }
}
// 字体大小
@for $i from 6 through 20 {
  .font#{$i*2} { font-size: $i*2+px+!important; }
}

// 宽度
@for $i from 4 through 180 {
  .w-#{$i*5} { width: $i*5+px+!important; }
}


