@charset "utf-8";
.w-button{
  @include radius;
  @extend %transition-all;
  display:inline-block;
  line-height:1;
  white-space:nowrap;
  cursor:pointer;
  background:#fff;
  border:1px solid $line;
  color:$gray;
  -webkit-appearance:none;
  text-align:center;
  box-sizing:border-box;
  outline:0;
  margin:0;
  -moz-user-select:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  height: $height-m;
  padding:0 10px;
  font-size:14px;
  &+&{
    margin-left:10px;
  }
  &:hover{
    color:$blue;
    border-color:$blue;
  }
  &.is-active,&:active{
    color:$d-blue;
    border-color:$d-blue;
    outline:0;
  }
  [class*=w-icon-]+span{
    margin-left:5px;
  }
  &.is-loading{
    position:relative;
    pointer-events:none;
  }
  &.is-loading:before{
    pointer-events:none;
    content:'';
    position:absolute;
    left:-1px;
    top:-1px;
    right:-1px;
    bottom:-1px;
    border-radius:inherit;
    background-color:rgba(255,255,255,.35);
  }
  &.is-disabled,
  &.is-disabled:hover{
    color:$disable-color;
    cursor:not-allowed;
    background-image:none;
    background-color:$disable-bg;
    border-color:$disable-line;
  }
  &.is-disabled.w-button_text{
    background-color:transparent;
  }
}

// 颜色按钮
.w-button_primary{
  color:#fff;
  background-color:$blue;
  border-color:$blue;
  &:hover{
    background:$l-blue;
    border-color:$l-blue;
    color:#fff;
  }
  &.is-active,&:active{
    background:$d-blue;
    border-color:$d-blue;
    color:#fff;
    outline:0;
  }
}
.w-button_success{
  color:#fff;
  background-color:$green;
  border-color:$green;
  &:hover{
    background:$l-green;
    border-color:$l-green;
    color:#fff;
  }
  &.is-active,&:active{
    background:$d-green;
    border-color:$d-green;
    color:#fff;
    outline:0;
  }
}
.w-button_warning{
  color:#fff;
  background-color:$yellow;
  border-color:$yellow;
  &:hover{
    background:$l-yellow;
    border-color:$l-yellow;
    color:#fff;
  }
  &.is-active,&:active{
    background:$d-yellow;
    border-color:$d-yellow;
    color:#fff;
    outline:0;
  }
}
.w-button_danger{
  color:#fff;
  background-color:$red;
  border-color:$red;
  &:hover{
    background:$l-red;
    border-color:$l-red;
    color:#fff;
  }
  &.is-active,&:active{
    background:$d-red;
    border-color:$d-red;
    color:#fff;
    outline:0;
  }
}

.w-button_large{
  height: $height-l;
  padding:0 19px;
  font-size:16px;
}
.w-button_small{
  height: $height-s;
  padding:0 9px;
  font-size:12px;
}
.w-button_text{
  border:none;
  color:$blue;
  background:0 0;
  &:hover{
    color:$l-blue;
  }
  &:active{
    color:$d-blue;
  }
}


.w-button-group{
  display:inline-block;
  vertical-align:middle;
}.w-button-group:after,.w-button-group:before{
  display:table;
  content:"";
}.w-button-group:after{
  clear:both;
}.w-button-group .w-button_primary:first-child{
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_primary:last-child{
  border-left-color:rgba(255,255,255,.5);
}.w-button-group .w-button_primary:not(:first-child):not(:last-child){
  border-left-color:rgba(255,255,255,.5);
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_success:first-child{
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_success:last-child{
  border-left-color:rgba(255,255,255,.5);
}.w-button-group .w-button_success:not(:first-child):not(:last-child){
  border-left-color:rgba(255,255,255,.5);
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_warning:first-child{
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_warning:last-child{
  border-left-color:rgba(255,255,255,.5);
}.w-button-group .w-button_warning:not(:first-child):not(:last-child){
  border-left-color:rgba(255,255,255,.5);
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_danger:first-child{
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_danger:last-child{
  border-left-color:rgba(255,255,255,.5);
}.w-button-group .w-button_danger:not(:first-child):not(:last-child){
  border-left-color:rgba(255,255,255,.5);
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_info:first-child{
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button_info:last-child{
  border-left-color:rgba(255,255,255,.5);
}.w-button-group .w-button_info:not(:first-child):not(:last-child){
  border-left-color:rgba(255,255,255,.5);
  border-right-color:rgba(255,255,255,.5);
}.w-button-group .w-button{
  float:left;
  position:relative;
}.w-button-group .w-button+.w-button{
  margin-left:0;
}.w-button-group .w-button:first-child{
  border-top-right-radius:0;
  border-bottom-right-radius:0;
}.w-button-group .w-button:last-child{
  border-top-left-radius:0;
  border-bottom-left-radius:0;
}.w-button-group .w-button:not(:first-child):not(:last-child){
  border-radius:0;
}.w-button-group .w-button:not(:last-child){
  margin-right:-1px;
}.w-button-group .w-button.is-active,.w-button-group .w-button:active,.w-button-group .w-button:focus,.w-button-group .w-button:hover{
  z-index:1;
}


