.w-select{
  position: relative;
  height: $height-m;
  font-size: 14px;
  &.is-disabled{
    .w-select_inner{
      border: 1px solid $disable-line;
      background-color: $disable-bg;
      color: $disable-color;
    }
    &:after{
      color: $disable-color;
      cursor: not-allowed;
    }
    &:hover{
      .w-select_inner{
        border-color:$disable-line;
      }
      &:after{
        color: $disable-color;
        cursor: not-allowed;
      }
    }
  }
  &:hover{
    .w-select_inner{
      border-color:$blue;
    }
    &:after{
      color:$blue;
    }
  }
  &:after{
    @extend %transition-all;
    position: absolute;
    top: 6px;
    right: 10px;
    font-family:"icon" !important;
    font-size:20px;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\e613";
    color: $icon;
  }
  .w-select_search{
    border:none;
    padding: 3px 10px;
    line-height: 1.5;
    box-sizing: border-box;
    display: block;
    width: 100%;
  }
  .w-select_inner{
    @extend %transition-all;
    @include radius;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: none;
    border: 1px solid $line;
    box-sizing: border-box;
    color: $gray;
    display: block;
    height: $height-m;
    line-height: 1;
    outline: none;
    padding: 3px 35px 3px 10px;
    width: 100%;
  }
  .w-select_content{
    background-color:#fff;
    border: 1px solid $line;
    border-top: none;
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    z-index: 1;
    line-height: 1.5;
    font-size: 14px;
    .w-select_option{
      max-height: 300px;
      overflow: auto;
    }
    li{
      @extend %transition-all;
      position: relative;
      padding: 3px 10px;
      cursor: pointer;
      &.is-disabled{
        cursor: not-allowed;
        background-color:#fff!important;
        color:$disable-color!important;
      }
      &:hover,&.is-selected{
        background-color:$blue;
        color: #fff;
      }
      &.is-selected:after{
        position: absolute;
        right: 10px;
        top: 2px;
        font-family:"icon" !important;
        font-size:16px;
        font-style:normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: "\e604";
      }
    }
    .is-multiple{
      li{
        &:hover{
          background-color:$blue;
          color: #fff;
        }
        &.is-selected{
          background-color: #fff;
          color:$blue;
          &:hover{
            background-color:$blue;
            color: #fff;
          }
        }
      }
    }
  }
}



