.w-pagination{
  font-size: 14px;
  display: inline-block;
  text-align: center;
  width: 100%;
  .pagination-list{
    display: inline-block;
    li{
      display:inline-block;
      &.active{
        a{
          color:#fff;
          background-color: $blue;
          border-color:$blue;
        }
      }
      &.disabled{
        a{
          color:$disable-color;
          cursor:not-allowed;
          background-image:none;
          background-color:$disable-bg;
          border-color:$disable-line;
        }
      }
      &.page-number.disabled{
        a{
          border:none;
          background-color:#fff;
          padding:0 5px;
        }
      }
    }
    li+li{
      margin-left: 5px;
    }
    a{
      @extend %transition-all;
      @include radius;
      -webkit-appearance:none;
      text-align:center;
      box-sizing:border-box;
      outline:0;
      margin:0;
      -moz-user-select:none;
      -webkit-user-select:none;
      -ms-user-select:none;
      font-size:14px;
      display: block;
      color: $gray;
      height: $height-m;
      line-height:$height-m;
      padding:0 15px;
      white-space:nowrap;
      cursor:pointer;
      background:#fff;
      border:1px solid $line;
      &:hover{
        color:$blue;
        border-color:$blue;
      }
    }
  }
  .total{
    margin-left: 20px;
    em{
      color: $blue;
    }
  }
}


