.w-dialog,.w-dialog_mask{
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
.w-dialog_mask{
  @include opacity;
  background: #000;
}
.w-dialog{
  position: fixed;
  .w-dialog_content{
    @include radius;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 101;
    padding: 15px;
    background: #fff;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
  }
  .w-dialog_header{
    position: relative;
    padding-bottom: 10px;
    border-bottom: 1px solid #e9e9e9;
    .w-dialog_title{
      font-size: 16px;
    }
    .w-dialog_close{
      @extend %transition-all;
      position: absolute;
      right: 0;
      top: 0;
      cursor: pointer;
      &:hover{
        color: $blue;
      }
    }
  }
  .w-dialog_body{
    margin:10px 0;
    font-size: 14px;
    max-height:calc(100% - 85px);
    overflow: auto;
  }
  &.is-alert{
    .w-dialog_body{
      font-size: 16px;
      margin: 30px 0;
    }
    [class^="w-icon-"], [class*=" w-icon-"]{
      font-size: 20px;
    }
    .w-icon-success{
      color: $green;
    }
    .w-icon-warning{
      color: $yellow;
    }
    .w-icon-danger{
      color: $red;
    }
  }
  .w-dialog_footer{
    padding:10px 15px 5px 15px;
    position: absolute;
    bottom:10px;
    left: 0;
    right: 0;
    text-align: right;
  }
}


