.w-tabs {
  position: relative;
  .w-tab_list {
    font-size: 14px;
    li{
      @extend %transition-all;
      position: relative;
      cursor: pointer;
      a{
        @extend %transition-all;
        color: $gray;
        display: block;
        &:hover{
          color: $blue;
        }
      }
      &.is-active{
        a{
          color: $blue;
        }
      }
      &.is-disabled {
        color: $disable-color;
        cursor: not-allowed;
        &:hover{
          color: $disable-color;
        }
        a{
          color: $disable-color;
          pointer-events: none;
        }
      }
    }
  }
  .w-tab_content {
    font-size: 14px;
    position: relative;
  }
  .w-tab_pane {
    padding:10px;
    &.is-active {
      transform: translateZ(0);
    }
    &[class*="leave-active"] {
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transform: translateX(0);
      transform: translateY(0);
    }
  }
  &.is-top {
    .w-tab_list{
      border-bottom:1px solid $line;
      li{
        top: 1px;
        display: inline-block;
        margin: 0;
        line-height: 24px;
        border-bottom: 1px solid $line;
        &.is-active{
          border-bottom: 1px solid $blue;
        }
        a{
          padding:0 10px;
        }
      }
    }
    .w-tab_content {
      overflow: hidden;
    }
  }
  &.is-bottom {
    .w-tab_list{
      border-top:1px solid $line;
      li{
        bottom: 1px;
        display: inline-block;
        margin: 0;
        line-height: 24px;
        border-top: 1px solid $line;
        &.is-active{
          border-top: 1px solid $blue;
        }
        a{
          padding:0 10px;
        }
      }
    }
    .w-tab_content {
      overflow: hidden;
    }
  }
  &.is-left{
    overflow-x: hidden;
    overflow-y: auto;
    .w-tab_list{
      @include float;
      width: 100px;
      border-right: 1px solid $line;
      li{
        @extend %text-overflow;
        right: -1px;
        display: block;
        max-width: 100%;
        border-right: 1px solid $line;
        text-align: right;
        line-height: auto;
        &:hover {
          border-right-color: $blue;
        }
        &.is-active {
          border-right-color: $blue;
        }
      }
      a{
        padding:5px 10px 5px 0;
      }
    }
    .w-tab_content{
      @include float;
      width: calc(100% - 120px);
    }
    .w-tab_pane{
      padding:5px 10px;
    }
  }
  &.is-right {
    overflow-x: hidden;
    overflow-y: auto;
    .w-tab_list{
      @include float(right);
      width: 100px;
      border-left: 1px solid $line;
      li{
        @extend %text-overflow;
        left: -1px;
        display: block;
        max-width: 100%;
        border-left: 1px solid $line;
        text-align: left;
        line-height: auto;
        &:hover {
          border-left-color: $blue;
        }
        &.is-active {
          border-left-color: $blue;
        }
        a{
          padding:5px 0 5px 10px;
        }
      }
    }
    .w-tab_content{
      @include float;
      width: calc(100% - 101px);
    }
    .w-tab_pane{
      padding:5px 10px;
    }
  }
}
.is-slideshow{
  .w-tab_content{
    margin: 0;
  }
  .w-tab_list{
    display: block;
    position: absolute;
    bottom: 10px;
    left: 50%;
    z-index: 15;
    width: 60%;
    padding-left: 0;
    margin-left: -30%;
    border: none;
    text-align: center;
    list-style: none;
    li{
      display: inline-block;
      width: 10px;
      height: 10px;
      margin: 1px 2px;
      text-indent: -999px;
      cursor: pointer;
      background-color: #000 \9;
      background-color: rgba(0,0,0,0);
      border: 1px solid #fff;
      border-radius: 10px;
      a{
        border: none;
      }
      &.is-active{
        background: #fff;
        a{
          /*background: #fff;*/
        }
      }
    }
  }
}



