@charset "utf-8";
//-----------------------------------------------------  
// mixin scss
// 包括常用的mixin，%
// mixin，通过@include调用，样式通过拷贝的方式使用，尤其适用于传递参数
// %，通过@extend调用，样式通过组合申明的方式使用，适用于不传参数的代码片段  
//-----------------------------------------------------
// @include float;
@mixin float($float:left) {
  float: $float;
  display: inline;
}
// @include opacity;
@mixin opacity($opacity:50) {
  opacity: $opacity / 100;
  filter: alpha(opacity=$opacity);
}
// @include radius;
@mixin radius($radius:2px) {
  border-radius: $radius;
}
// @extend %clear;
%clear{
  *zoom: 1;
  &:before,
  &:after {
    content: "";
    display: table;
    height: 0;
  }
  &:after {
    clear: both;
  }
}
// @extend %text-overflow;
%text-overflow{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; 
}
//@extend %transition-all;
%transition-all{
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -ms-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
//@extend %transition-input;
%transition-input{
  -webkit-transition: border-color .5s,-webkit-box-shadow .5s;
  -moz-transition: border-color .5s,-webkit-box-shadow .5s;
  -ms-transition: border-color .5s,-webkit-box-shadow .5s;
  -o-transition: border-color .5s,-webkit-box-shadow .5s;
  transition: border-color .5s,-webkit-box-shadow .5s;
}


