@charset "utf-8";
//基础样式
@import "_base";
@import "_mixin";
@import "_seting";
@import "_font";
@import "_animate";
// demo
.app ,body, html {
  // height: 100%;
}
.ishome{
  .head{
    background-color:transparent;
    border-bottom:1px solid rgba(255,255,255,0.5);
    margin-bottom: 0;
    position: absolute;
    z-index:2;
    left:0;
    right:0;
    a{
      @include opacity(100);
    }
  }
}
.wrap{
  width:1200px;
  margin:0 auto;
  color: $gray;
}
.head{
  height: 80px;
  line-height:80px;
  background-color: $blue;
  text-align: center;
  margin-bottom:10px;
}
h1.logo{
  @include float;
  font-size: 30px;
  color:#fff;
  a{
    color:#fff;
    font-size: 24px;
    margin-left:5px;
  }
}
.foot{
  height: 110px;
  line-height:110px;
  font-size:14px;
  text-align: center;
  border-top:1px solid $line;
}
.nav{
  @include float(right);
  a{
    @include opacity(70);
    height:77px;
    display:inline-block;
    color:#fff;
    font-size: 20px;
    margin-left:20px;
    padding:0 10px;
    &:hover{
      @include opacity(100);
    }
    &.active{
      @include opacity(100);
      border-bottom:3px solid #99d2fc;
    }
  }
}
.side-nav{
  @include float;
  font-size:14px;
  line-height:2;
  width: 200px;
  padding-bottom: 50px;
  dt{
    font-size: 18px;
    span{
      margin-left: 1em;
      display:block;
      font-size:12px;
    }
  }
  dd{
    margin-left: 2em;
    display: list-item;
    a{
      @extend %transition-all;
      color:$gray;
      &:hover,&.active{
        color:$blue;
      }
    }
  }
}
.banner {
  position: relative;
  height: 600px;
  width: 100%;
  overflow: hidden;
  background-color: #fff;
  ul{
    width: 100%;
    height: 100%;
  }
  li {
    position: absolute;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.banner-items {
  position: absolute;
  z-index: 10;
  bottom: 20px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-size: 0;
  span {
    display: inline-block;
    height: 6px;
    width: 30px;
    margin: 0 3px;
    background-color: #b2b2b2;
    cursor: pointer;
  }
  .active {
    background-color: #000;
  }
}
.list-enter-active {
  transition: all 0.3s ease;
  transform: translateX(0)
}
.list-leave-active {
  transition: all 0.3s ease;
  transform: translateX(-100%)
}
.list-enter {
  transform: translateX(100%)
}
.list-leave {
  transform: translateX(0)
}
.cards{
  @include float;
  @include radius(4px);
  text-align:center;
  width:298px;
  height:290px;
  border:1px solid $line;
  margin:40px 50px;
  position: relative;
  dt{
    font-size:18px;
    margin-bottom: 20px;
  }
  dd{
    font-size:14px;
    position: relative;
    p{
      padding:0 25px;
    }
    a{
      @extend %transition-all;
      display:block;
      width: 100%;
      height:50px;
      line-height:50px;
      color:$gray;
      &:hover{
        background:$blue;
        color:#fff;
      }
    }
  }
  .link{
    position: absolute;
    bottom:0;
    width: 100%;
    border-top:1px solid $line;
  }
}
.page{
  @extend %clear;
  padding-top: 35px;
  min-height:100%;
  h2{
    font-size:26px;
    margin-bottom: 20px;
    span{
      display:block;
      font-size:14px;
      margin-top:5px;
      color:$icon;
    }
    a{
      color: $blue;
    }
  }
  h3{
    font-size:20px;
    margin-bottom: 10px;
    span{
      display:block;
      font-size:14px;
      margin-top:5px;
      color:$icon;
    }
  }
}
.content{
  margin-left:220px;
}
