// 组件
.component{
  .test{
    padding:20px;
    border:1px solid #eee;
    border-bottom:none;
    border-radius:4px 4px 0 0;
  }
  .hello-world{
    font-size: 14px;
    margin-top: 10px;
    em{
      color: $green;
    }
  }
  pre{
    margin:0 0 40px 0;
    border:1px solid #eee;
    padding:0 20px;
    border-radius:0 0 4px 4px;
  }
  code{
    padding:0;
    margin:0;
    line-height:24px;
    font-size:14px;
  }
}
