// 动画页面
.transition-box{
  .transition{
    margin-top: 10px;
    height: 110px;
  }
  .transition-item{
    display: inline-block;
    margin-bottom: 10px;
    width: 200px;
    height: 100px;
    border-radius: 4px;
    background-color: $blue;
    text-align: center;
    color: #fff;
    padding: 40px 20px;
    margin-right: 20px;
    box-sizing: border-box;
  }
}
// 字体页面
.typography-box{
  .typo-box {
    margin-bottom: 20px;
    dl{
      height: 200px;
      width: 200px;
      position: relative;
      border: 1px solid #eaeefb;
      font-size: 40px;
      color: #1f2d3d;
      text-align: center;
      line-height: 162px;
      padding-bottom: 36px;
      box-sizing: border-box;
      display: inline-block;
      margin-right: 17px;
      border-radius: 4px;
    }
    dd{
      width: 100%;
      height: 35px;
      border-top: 1px solid #eaeefb;
      font-size: 14px;
      color: #8492a6;
      line-height: 35px;
      text-align: left;
      text-indent: 10px;
      font-family: Helvetica Neue;
    }
  }
  .typo-table{
    table{
      border-collapse: collapse;
      width: 100%;
      background-color: #fff;
      color: #5e6d82;
      font-size: 14px;
      margin-bottom: 45px;
      line-height: 1.5em;
      th,td{
        font-weight: normal;
        padding: 10px;
        border-bottom:1px solid #eaeefb;
      }
    }
  }
  .typo-PingFang {
    font-family: PingFang SC;
  }
  .typo-Hiragino {
    font-family: Hiragino Sans GB;
  }
  .typo-Microsoft {
    font-family: Microsoft YaHei;
  }
  .typo-Helvetica-neue {
    font-family: Helvetica Neue;
  }
  .typo-Helvetica {
    font-family: Helvetica;
  }
  .typo-Arial {
    font-family: Arial;
  }
}
// 图标页面
.icon-box{
  i{
    color: #8492a6;
    margin: 0 20px;
  }
  .icon-lists{
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: 1px solid #eaeefb;
    border-radius: 4px;
    margin-bottom: 20px;
    li{
      float: left;
      width: 16.66%;
      text-align: center;
      height: 80px;
      padding-top:40px;
      color: #666;
      font-size: 13px;
      transition: color .15s linear;
      border-right: 1px solid #eee;
      border-bottom: 1px solid #eee;
      margin-right: -1px;
      margin-bottom: -1px;
      i{
        display: block;
        font-size: 24px;
        margin-bottom: 15px;
        color: #8492a6;
      }
    }
  }
}
