.w-notify {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;
  width: 250px;
  // pointer-events: none;
  .w-notify_inner{
    @include radius;
    position: relative;
    margin: 10px;
    font-size: 14px;
    color: $gray;
    padding:10px 30px 10px 10px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    .w-icon-close{
      position: absolute;
      right: 10px;
      top: 13px;
      cursor: pointer;
      font-size: 14px;
    }
    .w-notify_icon{
      font-size: 20px;
      position: absolute;
      width: 20px;
      height: 20px;
      line-height: 20px;
      display: inline-block;
      left: 10px;
      top: 50%;
      margin-top: -10px;
    }
    &.has-icon{
      padding-left: 40px;
    }
    &.is-default{
      background-color: #fff;
    }
    &.is-primary{
      background-color:#CCE1FA;
      color: #2E72C5;
    }
    &.is-success{
      background-color:#CFF1A6;
      color: #6B9637;
    }
    &.is-warning{
      background-color:#FFE5A8;
      color: #C48E08;
    }
    &.is-danger{
      background-color:#FCC0C0;
      color: #BF2222;
    }
  }
  &.top-center{
    right: 0;
    left: 0;
    margin: 0 auto;
  }
  &.top-left{
    right: auto;
    left: 0;
  }
  &.bottom-left{
    top: auto;
    bottom: 0;
    right: auto;
    left: 0;
  }
  &.bottom-center{
    top: auto;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0 auto;
  }
  &.bottom-right{
    top: auto;
    bottom: 0;
  }
}



