.w-input{
  position: relative;
  height: $height-m;
  font-size:14px;
  &.w-input_large{
    height: $height-l;
    font-size:16px;
    .w-input_inner{
      height: $height-l;
    }
    .w-input_icon{
      top: 13px;
    }
  }
  &.w-input_small{
    height: $height-s;
    font-size:12px;
    .w-input_inner{
      height: $height-s;
    }
    .w-input_icon{
      top: 6px;
    }
  }
  &.is-disabled{
    .w-input_inner{
      border: 1px solid $disable-line;
      background-color: $disable-bg;
      color: $disable-color;
      &:hover,&:focus{
        border-color:$disable-line;
        ~.w-input_icon{
          color: $disable-color;
        }
      }
    }
  }
  .w-input_inner{
    @include radius;
    @extend %transition-all;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: none;
    border: 1px solid $line;
    box-sizing: border-box;
    color: $gray;
    display: block;
    height: $height-m;
    line-height: 1;
    outline: none;
    padding: 3px 10px;
    width: 100%;
    &:hover,&:focus{
      border-color:$blue;
      ~.w-input_icon{
        color: $blue;
      }
    }
  }
  .w-input_icon{
    @extend %transition-all;
    position: absolute;
    top: 10px;
    right: 10px;
    color: $icon;
  }
}
.w-textarea{
  font-size:14px;
  &.is-disabled{
    .w-textarea_inner{
      border: 1px solid $disable-line;
      background-color: $disable-bg;
      color: $disable-color;
      &:hover,&:focus{
        border-color:$disable-line;
      }
    }
  }
  .w-textarea_inner{
    @extend %transition-all;
    @include radius;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: none;
    border: 1px solid $line;
    box-sizing: border-box;
    color: $gray;
    display: block;
    line-height: 1.5;
    outline: none;
    padding: 5px 10px;
    width: 100%;
    &:hover,&:focus{
      border-color:$blue;
      ~.w-input_icon{
        color: $blue;
      }
    }
  }
}


